//colors
$main-color: rgb(52, 80, 161);
$secondary-color: rgb(4, 25, 85);

.main {
    display: flex;
    flex-direction: column;

    // border: 1px solid white;
    padding: 5px;
    width: 100%;



    h2 {
        color: white;
        text-align: center;
        font-size: 28px;
    }

    &__tasks {
           
    }

    &__row {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-width: 800px;
        margin: 0 auto;
        gap: 40px;

        @media (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__col {
        flex: 1 1 50%;
    }

    &__newTask {
        display: flex;
        justify-content: space-between;
        align-items: center;
        // gap: 10px;
        // background: $secondary-color;
        background: white;
        
        width: 100%;
        border: 0 transparent;
        border-radius: 20px;
        max-width: 800px;
        margin: 0 auto;
        margin-bottom: 20px;
        margin-top: 20px;
        

        

        button {
            display: flex;
            justify-content: center;
            align-items: center;
            border: 10px transparent;
            border-radius: 100%; 
        }
    }

    &__input {
        // background: $secondary-color;
        // color: white;
        width: 100%;
        border: 10px transparent;
        border-radius: 20px;
        padding: 10px;
        height: 100%;
        padding: 0 0 0 20px;
        // border: 1px solid white; 
    }

}