$main-color: rgb(52, 80, 161);
$secondary-color: rgb(4, 25, 85);

.menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 20px;
    background: $secondary-color;
    background: white;
    border: 0 transparent;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    color: white;
    min-height: 100vh;

    @media (max-width: 767px) {
        flex-direction: row;
        gap: 20px;
    }

    @media (max-width: 767px) {
        min-height: initial;
        border-radius: 0;
        border-bottom-right-radius: 30px;
        border-bottom-left-radius: 30px;
      }

    

    &__avatar {
        img {
            max-width: 64px;
            max-height: 64px;
            width: 100%;
            margin: 0 0 20px 0;
            border: 0 transparent;
            border-radius: 100%;
            
        }
    }

    &__avatar:hover {
        img {
            outline: 1px solid grey;
        }
        
    }


    &__link{
        display: block;
        color: black;
        font-size: 20px;
        margin: 0 0 20px 0;
        border-bottom: 1px solid lightgrey;
        padding: 0 0 5px 0;

        @media (max-width: 767px) {
            display: inline-flex;
            margin: 0 20px 0 0;
        }
    }





    
}

