//colors
$main-color: rgb(52, 80, 161);
$secondary-color: rgb(4, 25, 85);

.taskItem {

        display: flex;
        justify-content: space-between;
        align-items: center;
        
        // background: $secondary-color;
        background: white;
        padding: 0 0 0 10px;
        margin: 10px 0;
        width: 100%;
        border: 0 transparent;
        border-radius: 20px;
        
        span {
            width: 100%;
            // color: white;
            color: black;
            word-wrap: break-word;
        }

        &__checked {
            text-decoration: line-through;
        }

        // input {
        //     background: $secondary-color;
        //     width: 100%;
        //     color: white;
            
        //     border: 1px solid white; 
        // }

        button {
            display: flex;
            justify-content: center;
            align-items: center;
            border: 0 transparent;
            border-radius: 100%;
        }

}